import React from 'react';
import {graphql} from "gatsby";
import Layout from '../components/layout';
import Background from '../components/background';
import "../styles/templates/workTemplate.scss";
import PageTransition from 'gatsby-plugin-page-transitions';

export default ({data}) =>{

    const { frontmatter, html } = data.markdownRemark;

    return(
        <Layout>
          <PageTransition>
            <Background className="hero" subtitle={frontmatter.onBanner}/>
            <section className="text-content">
              <div className="columns is-full is-mobile is-multiline is-centered">
                  <h1 className="column is-full">{frontmatter.title}</h1>
                  <div className="column is-full" dangerouslySetInnerHTML={{ __html: html }}/>
              </div>
            </section>
            </PageTransition>
        </Layout>
    )
}

export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        path
        title
        onBanner
      }
    }
  }
`